import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { Auth } from 'src/app/auth';

@Component({
  selector: 'app-loginusers',
  templateUrl: './loginusers.component.html',
  styles: []
})
export class LoginusersComponent implements OnInit {
  auths: Auth[];
  password:string;
  user:string;
  
  constructor(private auth: AuthService,private router: Router,private location: Location) { 
    this.auth.getUser()
      .subscribe(auths => {
        console.log(auths);
        this.auths = auths;
      });
  }


  ngOnInit() {
  }


  loginUser(event){
    event.preventDefault();
    const username = this.user;
    const userpass = this.password;
        for(let i = 0; i < this.auths.length; i++) {
          if(this.auths[i].user == username && this.auths[i].password == userpass) {
            if(this.auths[i].auth == '1'){
              this.auth.setUser(username);
              this.router.navigate(['admin']);
            }
            if(this.auths[i].auth == "2"){
              this.auth.setUser(username);
              this.router.navigate(['admin2']);
            }
            if(this.auths[i].auth == "3"){
              this.auth.setUser(username);
              this.router.navigate(['users']);
              location.reload();
            }
          }
          console.log('tenes algun error');
        }
  }


}

