import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './components/admin/admin.component';
import { AdministradoresComponent } from './components/administradores/administradores.component';
import { TasksComponent } from './components/tasks/tasks.component';
import { MenuComponent } from './components/menu/menu.component';
import { LoginComponent } from './components/login/login.component';
import { LoginusersComponent } from './components/loginusers/loginusers.component';
import { UsuariosComponent } from './components/usuarios/usuarios.component';
import { Agregar23Component } from './components/agregar23/agregar23.component';

const routes: Routes = [
  { path: 'admin', component: AdminComponent },
  { path: 'administradores', component: AdministradoresComponent },
  { path: 'admin2', component: TasksComponent },
  { path: 'newuser', component: LoginComponent },
  { path: 'signin', component: LoginusersComponent },
  { path: 'users', component: UsuariosComponent },
  { path: 'agregar23', component: Agregar23Component },
  { path: '', component: MenuComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
