import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { TaskService } from '../../services/task.service';
import { Router } from '@angular/router';
import { Auth } from '../../auth';
import { Task } from '../../Task';
import { Sale } from '../../sale';

@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  providers: [AuthService,TaskService]
})
export class UsuariosComponent implements OnInit {
  auths: Auth[];
  tasks: Task[];
  tasks2: Task[];
  sale: Sale[];
  user:string;
  user2:string;
  password:string;
  auth:string;
  restaurant:string;
  client:string;
  description:string;
  cantidad:string;
  producto:string;



  constructor(private authsevice: AuthService,private taskService: TaskService,private router: Router) { 
    this.authsevice.getUserauth(2)
      .subscribe(users => {
        console.log(users);
        this.auths = users;
      });
    this.taskService.getTasks()
      .subscribe(tasks => {
        console.log(tasks);
        this.tasks = tasks;
    });  
    this.taskService.getSales()
      .subscribe(tasks => {
        console.log(tasks);
        this.sale = tasks;
    });  
  }

  


  ngOnInit() {
    this.user2 = this.authsevice.getCurrentUser();
  }

  buy(id) {
    this.taskService.getTasks()
      .subscribe(tasks => {
        console.log(tasks);
        this.tasks2 = tasks;
    });  
  } 
 

  addSale(event){
    event.preventDefault();
     const newSale:Sale = {
      restaurant:'La chinita',
      client: 'raul',
      product:this.producto,
      cantidad:parseInt(this.cantidad),
      description:this.description,
      isDone: false
    };
    this.taskService.addSales(newSale)
      .subscribe(task => {
        this.sale.push(task);
        this.cantidad = '';
        this.description = '';
      })        
  }

  logoutUser(){
    this.authsevice.logoutUser();
    this.router.navigate(['']);
  }




}
