import { Component, OnInit } from '@angular/core';
import { TaskService } from '../../services/task.service';
import { AuthService } from '../../services/auth.service';
import { Task } from '../../Task';
import { Sale } from '../../sale';
import { Auth } from '../../auth';
@Component({
  selector: 'app-agregar23',
  templateUrl: './agregar23.component.html',
  providers: [AuthService,TaskService]
})
export class Agregar23Component implements OnInit {
  auths: Auth[];
  tasks: Task[];
  sale: Sale[];
  user:string;
  password:string;
  auth:string;
  restaurant:string;
  client:string;
  description:string;
  cantidad:string;
  producto:string;
  constructor(private authsevice: AuthService,private taskService: TaskService) {
    this.taskService.getSales()
      .subscribe(tasks => {
        console.log(tasks);
        this.sale = tasks;
    }); 
   }

  ngOnInit() {
  }

  buy(id) {
    this.taskService.getTasksp(id)
      .subscribe(tasks => {
        console.log(tasks);
        this.tasks = tasks;
    });  
  } 
 

  addSale(event){
    event.preventDefault();
     const newSale:Sale = {
      restaurant:'La chinita',
      client: 'raul',
      product:this.producto,
      cantidad:parseInt(this.cantidad),
      description:this.description,
      isDone: false
    };
    this.taskService.addSales(newSale)
      .subscribe(task => {
        this.sale.push(task);
        this.cantidad = '';
        this.description = '';
        this.tasks.splice(0, 1);
      })        
  } 

}
