import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Auth } from '../auth';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  domain: string = "http://localhost:3000";
  constructor(private http: HttpClient) { }
  headers: HttpHeaders = new HttpHeaders({
    "Content-Type": "application/json"
  });
  

  getUser(){
    return this.http.get<Auth[]>(`${this.domain}/app/users`)
    .pipe(map(res => res));
  }

  getUserauth(auth){
    return this.http.get<Auth[]>(`${this.domain}/app/users/${auth}`)
    .pipe(map(res => res));
  }

  setUser(username: string): void {
    let user_string = username;
    localStorage.setItem("currentUser", user_string);
  }

  getCurrentUser(): string {
    let user_string = localStorage.getItem("currentUser");
    let user: string = user_string;
    return user;
  }

  logoutUser() {
    localStorage.removeItem("currentUser");
  }

  addUser(newAuth: Auth){
    return this.http.post<Auth>(`${this.domain}/app/users`, newAuth)
    .pipe(map(res => res));
   }


   deleteUser(id) {
    return this.http.delete<Auth>(`${this.domain}/app/users/${id}`)
      .pipe(map(res => res));
  }


}
