import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Task } from '../Task';
import { Sale } from '../sale';
@Injectable({
  providedIn: 'root'
})
export class TaskService {
domain: string = "http://localhost:3000";
  constructor(private http: HttpClient) { }

//rutas para agregar alimentos por parte de los admin

  getTasks() {
    return this.http.get<Task[]>(`${this.domain}/api/tasks`)
      .pipe(map(res => res));
  }
  getTasksp(id) {
    return this.http.get<Task[]>(`${this.domain}/api/tasks/${id}`)
      .pipe(map(res => res));
  }
  getTasksn(propietario) {
    return this.http.get<Task[]>(`${this.domain}/api/tasks/${propietario}`)
      .pipe(map(res => res));
  }
 
  addTask(newTask: Task) {
    return this.http.post<Task>(`${this.domain}/api/tasks`, newTask)
      .pipe(map(res => res));
  }

  deleteTask(id) {
    return this.http.delete<Task>(`${this.domain}/api/tasks/${id}`)
      .pipe(map(res => res));
  }

  updateTask(newTask) {
    return this.http.put<Task>(`${this.domain}/api/tasks/${newTask._id}`, newTask)
      .pipe(map(res => res));
  }

//rutas para agregar compra por parte de usuarios
  getSales() {
    return this.http.get<Sale[]>(`${this.domain}/sal/sale`)
      .pipe(map(res => res));
  }

  getSalesres(restaurant) {
    return this.http.get<Sale[]>(`${this.domain}/sal/sale/${restaurant}`)
      .pipe(map(res => res));
  }
  getSalescli(client) {
    return this.http.get<Sale[]>(`${this.domain}/sal/sale/${client}`)
      .pipe(map(res => res));
  }
 
  addSales(newTask: Sale) {
    return this.http.post<Sale>(`${this.domain}/sal/sale`, newTask)
      .pipe(map(res => res));
  }

  deleteSale(id) {
    return this.http.delete<Sale>(`${this.domain}/sal/sale/${id}`)
      .pipe(map(res => res));
  }

  updateSale(newTask) {
    return this.http.put<Sale>(`${this.domain}/sal/sale/${newTask._id}`, newTask)
      .pipe(map(res => res));
  }


}

