import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { Auth } from 'src/app/auth';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styles: []
})
export class AdminComponent implements OnInit {
auths: Auth[];
auths1: Auth[];
auths2: Auth[];
user2:string;
user:string;
password:string;
auth:string;
  constructor(private Auth: AuthService,private Auth1: AuthService,private router: Router) {
    this.Auth.getUserauth(1)
      .subscribe(auths => {
        console.log(auths);
        this.auths = auths;
      });
      this.Auth1.getUserauth(2)
      .subscribe(auths1 => {
        console.log(auths1);
        this.auths1 = auths1;
      });
      this.Auth.getUserauth(3)
      .subscribe(auths => {
        console.log(auths);
        this.auths2 = auths;
      });
   }

  ngOnInit() {
    this.user2 = this.Auth.getCurrentUser();
  }
//crear metodo get para un usuario


//
  loginUser(event){
    event.preventDefault();
    const newAuth:Auth = {
      user: this.user,
      password:this.password,
      auth:this.auth
    };
    this.Auth.addUser(newAuth)
    .subscribe(auth => {
      this.auths.push(auth);
      this.auths1.push(auth);
      this.user = '';
      this.password = '';
      this.auth = '';
    })  ;
  }

  logoutUser(){
    this.Auth.logoutUser();
    this.router.navigate(['']);
  }

  deleteUser(id) {
    const response = confirm('are you sure to delete it?');
    if (response ){
      const auth = this.auths;
      this.Auth.deleteUser(id)
        .subscribe(data => {
          console.log(data.n);
          if(data.n == 1) {
            for(let i = 0; i < auth.length; i++) {
              if(auth[i]._id == id) {
                auth.splice(i, 1);
              }
            }
          }
        })
    }
  }
  
  deleteUser1(id) {
    const response = confirm('are you sure to delete it?');
    if (response ){
      const auth = this.auths1;
      this.Auth.deleteUser(id)
        .subscribe(data => {
          console.log(data.n);
          if(data.n == 1) {
            for(let i = 0; i < auth.length; i++) {
              if(auth[i]._id == id) {
                auth.splice(i, 1);
              }
            }
          }
        })
    }
  }
  deleteUser2(id) {
    const response = confirm('are you sure to delete it?');
    if (response ){
      const auth = this.auths2;
      this.Auth.deleteUser(id)
        .subscribe(data => {
          console.log(data.n);
          if(data.n == 1) {
            for(let i = 0; i < auth.length; i++) {
              if(auth[i]._id == id) {
                auth.splice(i, 1);
              }
            }
          }
        })
    }
  }

}
