import { Component, OnInit } from '@angular/core';
import { TaskService } from '../../services/task.service';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FileSelectDirective, FileUploader} from 'ng2-file-upload';
import {saveAs} from 'file-saver';
import { Task } from '../../Task';
import { Auth } from '../../auth';
@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.css'],
  providers: [TaskService,AuthService]
})
export class TasksComponent implements OnInit {
  tasks: Task[];
  tasks2: Task[];
  user: Auth[];
  image:string;
  title: string;
  title2:string;
  description: string;
  description2:string;
  precio: string;
  precio2:number;
  user2:string;
  uri: string ='http://localhost:3000/uploads/';
  attachmentList:any = [];

  uploader:FileUploader = new FileUploader({url:this.uri});
  constructor(private taskService: TaskService,private authsevice: AuthService,private router: Router) {
    this.taskService.getTasks()
      .subscribe(tasks => {
        this.tasks = tasks;
    });
    this.authsevice.getUser()
      .subscribe(tasks => {
        this.user = tasks;
    });
    this.uploader.onCompleteItem = (item:any, response:any , status:any, headers:any) => {
      this.attachmentList.push(JSON.parse(response));
  }

   }

  ngOnInit() {
    this.user2 = this.authsevice.getCurrentUser();
    
  }

  presentar() {
    
    
  }

  addTask(event){
    event.preventDefault();
     const newTask:Task = {
      image:this.image,
      propietario:this.user2,
      title: this.title,
      description:this.description,
      precio:parseFloat(this.precio),
      isDone: false
    };
    this.taskService.addTask(newTask)
      .subscribe(task => {
        this.tasks.push(task);
        this.title = '';
        this.description = '';
        this.precio = '';
      })        
  }

  logoutUser(){
    this.authsevice.logoutUser();
    this.user2 = '';
    this.router.navigate(['']);
  }
  
  deleteTask(id) {
    const response = confirm('are you sure to delete it?');
    if (response ){
      const tasks = this.tasks;
      this.taskService.deleteTask(id)
        .subscribe(data => {
          console.log(data.n);
          if(data.n == 1) {
            for(let i = 0; i < tasks.length; i++) {
              if(tasks[i]._id == id) {
                tasks.splice(i, 1);
              }
            }
          }
        })
    }
  }  

  updateStatus(task: Task) {
    var newTask = {            
      _id: task._id,
      title: task.title,
      description: task.description,
      precio: task.precio,
      isDone: !task.isDone
    };
    this.taskService.updateTask(newTask)
      .subscribe(res => {
        task.isDone = !task.isDone;
      })
  }

}

